<template>
  <v-app>
    <core-toolbar />
    <core-drawer />
    <v-container fill-height fluid grid-list-xl>
      <v-layout justify-center wrap>
        <v-flex md12>
          <material-card
            color="#00bcd4"
            flat
            full-width
            title="신청목록"
            text="Here is a subtitle for this table"
          >
            <v-data-table :headers="headers" :items="items.slice(0, 7)" hide-actions>
              <template slot="headerCell" slot-scope="{ header }">
                <span class="subheading font-weight-light text--darken-3" v-text="header.text" />
              </template>
              <template slot="items" slot-scope="{ item }">
                <td>{{ item.name }}</td>
                <td>{{ item.country }}</td>
                <td>{{ item.city }}</td>
                <td class="text-xs-right">{{ item.salary }}</td>
              </template>
            </v-data-table>
          </material-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>
<script>
export default {
  data: () => ({
    headers: [
      {
        sortable: false,
        text: "Name",
        value: "name"
      },
      {
        sortable: false,
        text: "Country",
        value: "country"
      },
      {
        sortable: false,
        text: "City",
        value: "city"
      },
      {
        sortable: false,
        text: "Salary",
        value: "salary",
        align: "right"
      }
    ],
    items: [
      {
        name: "Dakota Rice",
        country: "Niger",
        city: "Oud-Tunrhout",
        salary: "$35,738"
      }
    ]
  })
};
</script>
